import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaMix = null;
var canvaColumnMeter = null;


export default {
	data() {
		return {
			pixelRatio: 1,
			serverData: '',
			itemCount: 20, //x轴单屏数据密度
			// sliderMax: 50
		}
	},
	onLoad() {
		_self = this;











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.cWidth2 = uni.upx2px(700);
		this.cHeight2 = uni.upx2px(1100);
		this.cWidth3 = uni.upx2px(250);
		this.cHeight3 = uni.upx2px(250);
		
		this.getServerData();
	},
	onReady() {},
	methods: {
		getServerData() {
			uni.request({
				url: 'https://www.easy-mock.com/mock/5cc586b64fc5576cba3d647b/uni-wx-charts/chartsdata3',
				data: {},
				success: function(res) {
					console.log(res.data.data)
					//下面这个根据需要保存后台数据，我是为了模拟更新柱状图，所以存下来了
					let Mix = {
						categories: [],
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去

					Mix.categories = res.data.data.Mix.categories;
					Mix.series = res.data.data.Mix.series;

					_self.showMix("canvasMix", Mix);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},

		showMix(canvasId, chartData) {
			canvaMix = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'mix',
				fontSize: 11,
				legend: false,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				enableScroll: false, //开启图表拖拽功能
				xAxis: {
					disableGrid: false,
					type: 'grid',
					gridType: 'dash',
					itemCount: 4,
					scrollShow: true,
					scrollAlign: 'left',
				},
				yAxis: {
					gridType: 'dash',
					splitNumber: 5,
					min: 10,
					max: 180
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				dataLabel: false,
				dataPointShape: true,
				extra: {
					lineStyle: 'straight'
				},
			});
		},
		touchMix(e) {
			canvaMix.scrollStart(e);
		},
		moveMix(e) {
			canvaMix.scroll(e);
		},
		touchEndMix(e) {
			canvaMix.scrollEnd(e);
			//下面是toolTip事件，如果滚动后不需要显示，可不填写
			canvaMix.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
	}
}
